const rootRed = (state = main, action) => {
  if (action.type === "convLang") {
    switch (action.lang) {
      case "english":
        state = { ...state };
        state.lang = main.lang;
        break;
      case "indo":
        state = { ...state };
        state.lang = indoData;
        break;
      case "sasak":
        state = { ...state };
        state.lang = sasakData;
        break;
      case "arab":
        state = { ...state };
        state.lang = null;
        break;
      default:
        break;
    }
  }
  return state;
};

export default rootRed;
